let canvas = document.getElementById("canvas");
let thin = document.getElementById("thin");
let thick = document.getElementById("thick");
let black = document.getElementById("black");
let red = document.getElementById("red");
let yellow = document.getElementById("yellow");
let blue = document.getElementById("blue");
let eraser = document.getElementById("eraser");
let pen = document.getElementById("pen");
let clear = document.getElementById("clear");


canvas.width = document.documentElement.clientWidth;
canvas.height = document.documentElement.clientHeight;
let ctx = canvas.getContext("2d");

ctx.fillStyle = "black";
ctx.strokeStyle = "none";
ctx.lineWidth = 8;
ctx.lineCap = "round";//Á∫øÊù°ËΩ¨ÊäòÂ§Ñ‰∏∫ÂúÜÔºåËÄå‰∏çÊòØÁõ¥ËßíÈîØÈΩøÁä∂

let painting = false; //ÊéßÂà∂ÁîªÁîªËß¶ÂèëÊó∂Êú∫ÔºåÊòØÂê¶Êåâ‰∏ã
let eraserEnabled = false //ÊéßÂà∂Ê©°ÁöÆÊì¶Ëß¶ÂèëÊó∂Êú∫ÔºåÊòØÂê¶Êì¶Èô§
let lastPoint = [undefined,undefined];

monitor()
function monitor(){
  thin.onclick = ()=>{
    lineWidth = 5
    thin.classList.add('active')
    thick.classList.remove('active')
  }
  thick.onclick = ()=>{
    lineWidth = 8
    thick.classList.add('active')
    thin.classList.remove('active')
  }
  black.onclick = ()=>{
    black.classList.add("active")
    red.classList.remove("active")
    yellow.classList.remove("active")
    blue.classList.remove("active")
    ctx.strokeStyle = "black"
  }
  red.onclick = ()=>{
    red.classList.add("active")
    black.classList.remove("active")
    yellow.classList.remove("active")
    blue.classList.remove("active")
    ctx.strokeStyle = "red"
  }
  yellow.onclick = ()=>{
      yellow.classList.add("active")
      black.classList.remove("active")
      red.classList.remove("active")
      blue.classList.remove("active")
      ctx.strokeStyle = "yellow"
  }
  blue.onclick = ()=>{
      blue.classList.add("active")
      black.classList.remove("active")
      red.classList.remove("active")
      yellow.classList.remove("active")
      ctx.strokeStyle = "blue"
  }
  eraser.onclick = ()=>{
    eraserEnabled = true
    painting = false//ÈúÄË¶ÅÂêåÊó∂Â∞Üpainting ÁΩÆ‰∏∫falseÔºåÂê¶Âàô‰ºöÁîªÁ¨îÂíåÊ©°ÁöÆÊì¶‰ºöÂêåÊó∂ÂºÄÂêØ
    eraser.classList.add('active')
    pen.classList.remove('active')
  }
  pen.onclick = ()=>{
    eraserEnabled = false
    // painting = true//‰∏çËÉΩÂºÄÂêØÔºåÂê¶ÂàôÈº†Ê†áÊ≤°ÊúâÊåâ‰∏ãÂ∞±ÂºÄÂßãÁîªÁîª
    pen.classList.add("active")
    eraser.classList.remove("active")
  }
  clear.onclick = ()=>{
    ctx.clearRect(0,0,canvas.width,canvas.height)
  }
  download.onclick = ()=>{
    let a = document.createElement('a')
    a.download = 'ÊàëÁöÑÊ∂ÇÈ∏¶.png'
    let url = canvas.toDataURL()
    a.href = url
    a.click()
  }
// ÂÖºÂÆπÊâãÊú∫
let isTouchDevice = "ontouchstart" in document.documentElement;//ÂîØ‰∏ÄÊ£ÄÊµãÊòØÂê¶‰∏∫ÁßªÂä®Á´ØÁöÑÊñπÊ≥ï
if (isTouchDevice) {
  canvas.ontouchstart = (e) => { //ÂºÄÂßãËß¶Êë∏
    painting = true
    let x = e.touches[0].clientX;
    let y = e.touches[0].clientY;
    lastPoint = [x, y]; //ËÆ∞‰∏ãÁ¨¨‰∏ÄÊ¨°Èº†Ê†áÁÇπÂáªÁöÑ‰ΩçÁΩÆÔºå‰ª•‰Ωú‰∏∫ÁîªÁ∫øÁöÑÁªàÁÇπÁöÑÂèÇËÄÉÁÇπ
  };
  canvas.ontouchmove = (e) => { //ÊâãÊåáÁßªÂä®
    let x = e.touches[0].clientX;
    let y = e.touches[0].clientY;
    if(painting){
      drawLine(lastPoint[0], lastPoint[1], x, y);
      lastPoint = [x, y];//ÈúÄË¶ÅÂÆûÊó∂Êõ¥Êñ∞ÁîªÁ∫øÁöÑ Ëµ∑ÁÇπÔºåËÆ∞ÂΩïÊØèÊ¨°ÁÇπÂáª‰ΩçÁΩÆ
      if(eraserEnabled){   
        ctx.clearRect(x-10,y-10,30,30)
      }
    }


  };
  canvas.ontouchend = ()=>{ //ÁªìÊùüËß¶Êë∏
    painting = false
  }
} else {
  //PCÁ´Ø
  canvas.onmousedown = (e) => { //Èº†Ê†áÊåâ‰∏ã
    painting = true;
    lastPoint = [e.clientX, e.clientY];
  };

  canvas.onmousemove = (e) => { //Èº†Ê†áÁßªÂä®
    let x = e.clientX
    let y = e.clientY
    if(painting){
      drawLine(lastPoint[0], lastPoint[1],x,y );
      lastPoint = [x, y];
      if(eraserEnabled){
        ctx.clearRect(x-10,y-10,30,30)
      }
    }

  };
  canvas.onmouseup = () => { //Èº†Ê†áÊä¨Ëµ∑
    painting = false;
  };
}

}




function drawLine(x1, y1, x2, y2) {
  ctx.beginPath();
  ctx.moveTo(x1, y1);//Ëµ∑ÁÇπ
  ctx.lineTo(x2, y2);//ÁªàÁÇπ
  ctx.stroke(); //ÊèèËæπ
}
//Ê∏ÖÈô§Á¨îËøπ
//Êõ¥Êç¢ÁîªÁ¨îÈ¢úËâ≤
//ÈáçÁΩÆÁîªÊùø
